  0% {
    -webkit-transform: scale(0.6);
    transform: scale(0.6);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
}
@-webkit-keyframes rcSelectChoiceZoomOut {
  to {
    -webkit-transform: scale(0);
    transform: scale(0);
    opacity: 0;
  }
}
